:root {
  --primaryGreen: hsl(177, 100%, 33%);
  --greenDark: hsl(176, 100%, 10%);
  --greenLight: hsl(176, 50%, 73%);
  --primaryBlue: hsl(196, 100%, 33%);
  --primaryBlueDark: hsl(196, 100%, 10%);
  --darkBlue: hsl(196, 100%, 24%);
  --lightBlue: hsl(196, 49%, 73%);
  --primaryOrange: hsl(30, 92%, 54%);
  --darkOrange: hsl(30, 77%, 44%);
  --lightOrange: hsl(31, 91%, 82%);
  --medGrey: hsl(0, 0%, 44%);
  --dkGrey: hsl(0, 0%, 25%);
  --black:hsl(0, 0%, 6%);
  --white: hsl(240, 100%, 99%);
  --white60: hsla(240, 100%, 99%, .85);
}

:root {
  --font-heading: "Roboto Slab", Helvetica, Arial, sans-serif; /* heading font */
  --font-text: "Lato", Helvetica, Arial, sans-serif; /* body font */
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--white);
  color: var(--black);
}

img {
  display: block;
  width: 100%;
}

figure,
blockquote {
  margin: 0;
}

header ul,
footer ul {
  margin: 0;
  padding: 0;
}
header ul li,
footer ul li {
  list-style-type: none;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  width: auto;
  height: auto;
  background-color: #fff;
  padding: 0.5rem;
  border: 1px solid;
}

h1,
h2,
h3 {
  font-family: var(--font-heading);
  font-weight: 700;
  margin-top: 0;
  letter-spacing: -0.5px;
}

h1 {
  font-size: 3.75rem;
  font-size: clamp(1.875rem, 1.02rem + 3.632vw, 3.75rem);
  line-height: 1;
  margin-block-end: 1.25rem;
  color: var(--darkBlue);
}

p {
  font-size: 1rem;
  line-height: 1.3;
  margin-top: 0;
  margin-bottom: 1.25rem;
}

a:visited,
a:active {
  text-decoration: none;
}

a {
  text-decoration: underline;
}

.wrapper {
  width: min(100% - 3rem, 75rem);
  margin-inline: auto;
  padding-block: 60px;
}
@media (min-width: 56.25em) {
  .wrapper {
    padding-block: 60px;
  }
}

header {
  background-color: var(--white);
  box-shadow: 0 3px 6px var(--dkGrey);
}

.topnav, .topnav__menu {
  background-color: var(--white);
  z-index: 10;
}
.topnav__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: min(100% - 3rem, 75rem);
  margin-inline: auto;
}
.topnav__homelink {
  margin: 16px 0 16px;
}
@media (max-width: 43.74875em) {
  .topnav__homelink {
    z-index: 100;
    width: 65%;
    margin: 0;
  }
}
.topnav__open, .topnav__close {
  background: none;
  padding: 0;
  border: 0;
}
@media (max-width: 43.74875em) {
  .topnav__menu {
    position: fixed;
    inset: 0 0 auto 0;
    padding: 12px 24px 60px;
    border-radius: 0 0 20px 20px;
    outline: 2px solid yellow;
  }
}
.topnav__close {
  display: none;
}
@media (max-width: 43.74875em) {
  .topnav__close {
    display: block;
    margin-inline-start: auto;
    margin-block-end: 20px;
  }
}
.topnav__links {
  display: flex;
  flex-direction: row;
  gap: 30px;
}
@media (max-width: 43.74875em) {
  .topnav__links {
    flex-direction: column;
    align-items: center;
  }
}
.topnav__link {
  display: block;
  font-size: 1.125rem;
  line-height: 1;
  font-weight: 400;
  color: var(--primaryBlueDark);
  text-decoration: none;
}
.topnav__link:hover {
  color: var(--darkOrange);
}

.top__bar {
  background-color: var(--primaryBlue);
  width: 100%;
  height: 20px;
  z-index: 12;
  position: relative;
}

.featureImage {
  background: url(/img/gibsons_hero_1920x750.jpg) no-repeat;
  background-position: center top;
  background-size: cover;
  left: 50%;
  padding: 0;
  position: relative;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  margin-top: 0px;
  margin-bottom: 0px;
  overflow: hidden;
  height: 35.9375rem;
}
@media (max-width: 43.74875em) {
  .featureImage {
    background-position: left top;
    height: 28.125rem;
  }
}

.indexHero {
  margin-bottom: 1.5rem;
}

.hero-img {
  width: 100vw;
  height: auto;
  min-height: 18.75rem;
  overflow: hidden;
  position: relative;
  display: block;
  margin: 0 auto;
  top: 0;
}
@media screen and (max-width: 639px) {
  .hero-img {
    min-height: auto;
  }
}

img.full-width {
  width: 100%;
  height: auto;
  position: relative;
  display: block;
  padding: 0;
  margin: 0;
}

img.full-width {
  width: 100%;
  height: auto;
  position: relative;
  display: block;
  padding: 0;
  margin: 0;
}
@media screen and (max-width: 1023px) {
  img.full-width {
    width: auto;
    height: 45vh;
    left: 50%;
    transform: translate(-50%);
    max-width: none;
    overflow: hidden;
  }
}

.card {
  border: 1px solid var(--darkBlue);
}

.testPadding {
  padding: 1.875rem;
}

.artworkcard {
  width: 3.75rem;
}
.artworkcard__title {
  font-size: 1.125rem;
  color: var(--darkBlue);
}